export type XO = "X" | "O" | "-";

export class Game {
  cells: XO[] = ["-", "-", "-", "-", "-", "-", "-", "-", "-"];

  getCells(): XO[] {
    return this.cells;
  }

  getTurn(): XO {
    const takenCells = this.cells.filter((cell) => cell !== "-");
    return takenCells.length % 2 === 0 ? "X" : "O";
  }

  getWinner(): XO {
    if (this.cells[0] === this.cells[1] && this.cells[1] === this.cells[2]) {
      return this.cells[0];
    } else if (
      this.cells[3] === this.cells[4] &&
      this.cells[3] === this.cells[5]
    ) {
      return this.cells[3];
    } else if (
      this.cells[6] === this.cells[7] &&
      this.cells[6] === this.cells[8]
    ) {
      return this.cells[6];
    } else if (
      this.cells[0] === this.cells[3] &&
      this.cells[0] === this.cells[6]
    ) {
      return this.cells[0];
    } else if (
      this.cells[1] === this.cells[4] &&
      this.cells[1] === this.cells[7]
    ) {
      return this.cells[1];
    } else if (
      this.cells[2] === this.cells[5] &&
      this.cells[2] === this.cells[8]
    ) {
      return this.cells[2];
    } else if (
      this.cells[0] === this.cells[4] &&
      this.cells[0] === this.cells[8]
    ) {
      return this.cells[0];
    } else if (
      this.cells[2] === this.cells[4] &&
      this.cells[2] === this.cells[6]
    ) {
      return this.cells[2];
    }
    return "-";
  }

  isTie(): boolean {
    return false;
  }

  onClick(i: number): void {
    if (this.cells[i] === "-") {
      this.cells[i] = this.getTurn();
    }
  }

  restart(): void {
    console.log("restart called");
    new Game();
  }
}
